<template>
  <div class="flex-box tourist-attraction">
    <div class="flex-1 scroll province">
      <div class="list" :class="{ 'cur-province': label === cur.province}" :key="label" v-for="({ label, list }) in touristAttraction">
        <div class="title" @click="handleProvince(label, list)">{{ label }}</div>
      </div>
    </div>
    <div v-if="city.length > 0" class="flex-1 scroll city">
      <div class="list" :class="{ 'cur-city': label === cur.city}" :key="label" v-for="({ label, list }) in city">
        <div class="title" @click="handleCity(label, list)">{{ label }}<i>{{list.length}}</i></div>
      </div>
    </div>
    <div v-if="scenicSpot.length > 0" class="flex-2 scroll scenic-spot">
      <div class="list" :key="item" v-for="item in scenicSpot">
        <a :href="`https://www.baidu.com/s?ie=UTF-8&wd=${item}`" target="_blank">{{ item }}</a>
      </div>
    </div>
  </div>
</template>

<script>
import TouristAttraction from '@/utils/tourist-attraction.json'
export default {
  name: 'TouristAttraction',
  data () {
    return {
      touristAttraction: TouristAttraction,
      city: [],
      scenicSpot: [],
      cur: {
        province: '',
        city: ''
      }
    }
  },
  methods: {
    handleProvince (label, list) {
      if (label === this.cur.province) return
      this.cur.province = label
      this.cur.city = ''
      this.city = list
      this.scenicSpot = []
    },
    handleCity (label, list) {
      if (label === this.cur.city) return
      this.cur.city = label
      this.scenicSpot = list
    }
  }
}
</script>

<style scoped lang="less">

.tourist-attraction {
  width: 100%;
  .update-time {
    background: #ccc;
  }
  .list {
    line-height: 1.4;
    padding: 5px;
  }
  .title {
    cursor: pointer;
  }
  .province {
    background: #dedede;
  }
  .city, .cur-province {
    background: #f2f2f2;
  }
  .scenic-spot, .cur-city {
    background: #fff;
  }
}
</style>
